
body {
    margin-top: 0px;
    margin-left: 10px;
    margin-right: 10px;
}

#page_1 #id_1 {
    float: left;
    border: none;
    margin: 48px 0px 0px 1px;
    padding: 0px;
    border: none;
    width: 303px;
    overflow: hidden;
}

#page_1 #id_2 {
    float: left;
    border: none;
    margin: 66px 0px 0px 0px;
    padding: 0px;
    border: none;
    width: 465px;
    overflow: hidden;
}

#page_1 #p1dimg1 {
    position: absolute;
    top: 0px;
    left: 0px;
    z-index: -1;
    width: 721px;
    height: 780px;
}

#page_1 #p1dimg1 #p1img1 {
    width: 721px;
    height: 780px;
}

.headerCircle {
    display: -ms-inline-flexbox;
    display: inline-flex;
    background: #FFFFFF;
    border-radius: 50%;
    z-index: 100;
}

.headerBox {
    margin-left: 10%;
    position: absolute;
    top: 50px;
    width: 80%;
}

.headerText {
    text-align: right;
    text-transform: uppercase;
    margin-right: 10px;
    position: relative;
    top: 25%;
    bottom: 25%;
}

.sectionTitle {
    font: 19px 'Franklin Gothic Demi';
    text-transform: uppercase;

}

.sectionBar {
    display: block;
    width: 90%;
    height: 10px;
    margin-bottom: 10px;
}

.sectionContainer {
    margin: 0 5px 0 5px;
}

.description {
    font: 13px 'Rockwell';
}

.headline {
    font: 13px 'Franklin Gothic Demi';
    margin-bottom: 5px;
    text-transform: uppercase;
}

.dclr {
    clear: both;
    float: none;
    height: 1px;
    margin: 0px;
    padding: 0px;
    overflow: hidden;
}

.ft0 {
    font: 73px 'Franklin Gothic Demi';
    color: #ea4e4e;
}

.ft1 {
    font: 19px 'Franklin Gothic Demi';
}

.ft3 {
    font: 13px 'Rockwell';
    line-height: 15px;
}

.ft4 {
    font: 11px 'Rockwell';
    text-decoration: underline;
    color: #0563c1;
    line-height: 13px;
}

.ft5 {
    font: 33px 'Franklin Gothic Demi';
    line-height: 38px;
}

.ft6 {
    font: 15px 'Franklin Gothic Demi';
    line-height: 20px;
}

.ft7 {
    font: 13px 'Franklin Gothic Demi';
